import React, { useState, useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Form, Button, Image, Row, Col } from "react-bootstrap";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import { Link } from "react-router-dom";
import { listProductDetails, updateProduct } from "../actions/productActions";
import { PRODUCT_UPDATE_RESET } from "../constants/productConstants";
import axios from "axios";
import Loader from "../components/Loader";
import Message from "../components/Message";
import { refreshLogin, getUserDetails } from "../actions/userActions";
import Select from "react-select";
import FormContainer from "../components/FormContainer";
import MultiSelect from 'multiselect-react-dropdown';

const options = [
  { value: "Clothing", label: "Clothing"},
  { value: "Footwear", label: "Footwear" },
  { value: "Watches", label: "Watches" },
  { value: "HandBags", label: "HandBags" },
  { value: "Accessories", label: "Accessories" },
];

const subCategoryOptions = [
  {value :"Men", label:"Men" },
  {value :"Women", label: "Women"},
  {value :"Kids", label: "Kids"},
]

const sizeOptions = [
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' },
  { value: '10', label: '10' },
  { value: '11', label: '11' },
  {value : "41", label: "41"},
  {value : "42", label: "42"},
  {value : "43", label: "43"},
  {value : "44", label: "44"},
  {value : "45", label: "45"},
];

const clothingOptions =[
  {value : "S", label: "S"},
  {value : "M", label: "M"},
  {value : "L", label: "L"},
  {value : "XL", label: "XL"},
  {value : "XXL", label: "XXL"},
  {value : "XXXL", label: "XXXL"},
]

const ProductEditPage = ({ match, history }) => {
  // all variable for stroing product details
  const productId = match.params.id;
  const [name, setName] = useState("");
  const [brand, setBrand] = useState("");
  const [category, setCategory] = useState("");
  const [subCategory, setSubCategory] = useState("");
  const [description, setDescription] = useState("");
  // const [image, setImage] = useState("");
  const [images, setImages] = useState([]);
  const [price, setPrice] = useState(0.0);
  const [countInStock, setCountInStock] = useState(0);
  const [sizesAvailable, setSizesAvailable] = useState([]);
  const [numReviews, setNumReviews] = useState(0);
  const [dealer, setDealer] = useState("");
  const [sellingPrice, setSellingPrice] = useState(0.0);

  // to upload product image
  // const [uploading, setUploading] = useState(false);
  const [imagesUploading, setImagesUploading] = useState(false);
  // const [errorImageUpload, setErrorImageUpload] = useState("");
  const [errorImagesUpload, setErrorImagesUpload] = useState("");
  const dispatch = useDispatch();

  const productDetails = useSelector((state) => state.productDetails);
  const { loading, product, error } = productDetails;

  const productUpdate = useSelector((state) => state.productUpdate);
  const {
    loading: loadingUpdate,
    success: successUpdate,
    error: errorUpdate,
  } = productUpdate;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const userDetails = useSelector((state) => state.userDetails);
  const { error: userLoginError } = userDetails;

  // fetch user login details
  useEffect(() => {
    userInfo && dispatch(getUserDetails("profile"));
  }, [userInfo, dispatch]);

  // fetch new access tokens if user details fail, using the refresh token
  useEffect(() => {
    if (userLoginError && userInfo) {
      const user = JSON.parse(localStorage.getItem("userInfo"));
      user && dispatch(refreshLogin(user.email));
    }
  }, [userLoginError, dispatch, userInfo]);

  useEffect(() => {
    dispatch(listProductDetails(productId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // update the product details in state
  useEffect(() => {
    if (successUpdate) {
      dispatch({ type: PRODUCT_UPDATE_RESET });
      history.push("/admin/productlist");
    } else {
      if (!product || product._id !== productId) {
        dispatch(listProductDetails(productId));
      } else {
        setName(product.name);
        setPrice(product.price);
        // setImage(product.image);
        setImages(product.images);
        setBrand(product.brand);
        setCategory(product.category);
        setSubCategory(product.subCategory);
        setDescription(product.description);
        setCountInStock(product.countInStock);
        setNumReviews(product.numReviews);
        setSizesAvailable(product.sizesAvailable);
        setDealer(product.dealer);
        setSellingPrice(product.sellingPrice);
      }
    }
  }, [product, dispatch, productId, history, successUpdate]);

  // submit the product details
  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(
      updateProduct({
        _id: productId,
        name,
        brand,
        price,
        sellingPrice,
        category,
        subCategory,
        description,
        countInStock,
        // image,
        images,
        numReviews,
        sizesAvailable,
        dealer
      })
    );
  };

  // submit file to cloudinary bucket, get the url
  // const handleFileUpload = async (e) => {
  //   const file = e.target.files[0];
  //   const formData = new FormData();
  //   formData.append("file", file);
  //   setUploading(true);
  //   try {
  //     const config = {
  //       headers: {
  //         "Content-Type": "multipart/form-data",
  //       },
  //     };

  //     const { data } = await axios.post("/api/upload", formData, config);
  //     setImage(data.secure_url);
  //     setUploading(false);
  //   } catch (error) {
  //     setErrorImageUpload("Please choose a valid image");
  //     setUploading(false);
  //   }
  // };

  //mulitple file upload to cloudinary bucket, get the url
  const handleFilesUpload = async (e) => {
    const files = e.target.files;
    const formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      formData.append("files", files[i]);
    }
    setImagesUploading(true);
    try {
      const config = {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      };

      const { data } = await axios.post("/api/upload/images", formData, config);
      setImages(data.map((image) => image.secure_url));
      setImagesUploading(false);
    } catch (error) {
      setErrorImagesUpload("Please choose valid images");
      setImagesUploading(false);
    }
  };

  const onSelect = (selectedList) => {
    setSizesAvailable(selectedList);
  };

  const onRemove = (selectedList) => {
    setSizesAvailable(selectedList);
  };


  const [displayOptions, setDisplayOptions] = useState([]);

  useEffect(() => {
    if (category === "Footwear") {
      setDisplayOptions(sizeOptions);
    } else if (category === "Clothing") {
      setDisplayOptions(clothingOptions);
    } else {
      setDisplayOptions([]);
    }
  }, [category]);


return (
  <>
    <Link to="/admin/productlist">
      <Button variant="outline-primary" className="mt-3">
        Go Back
      </Button>
    </Link>
    <FormContainer style={{ marginTop: "-2em" }}>
      <h1>Edit Product</h1>
      {loadingUpdate ? (
        <Loader />
      ) : errorUpdate ? (
        <Message variant="danger" duration={10}>
          {errorUpdate}
        </Message>
      ) : (
        <>
          {loading ? (
            <Loader />
          ) : (
            <Form onSubmit={handleSubmit}>
              {error && (
                <Message variant="danger" duration={10}>
                  {error}
                </Message>
              )}
              <Form.Group controlId="name">
                <FloatingLabel
                  controlId="nameinput"
                  label="Name"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter Name"
                    type="text"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="price">
                <FloatingLabel
                  controlId="priceinput"
                  label="Price"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter price"
                    type="number"
                    value={price}
                    min="0"
                    max="10000"
                    step="0.1"
                    onChange={(e) => setPrice(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="price">
                <FloatingLabel
                  controlId="actualPriceinput"
                  label="ActualPrice"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter Actual Price"
                    type="number"
                    value={sellingPrice}
                    min="0"
                    max="10000"
                    step="0.1"
                    onChange={(e) => setSellingPrice(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              {/* single file */}
              {/* {errorImageUpload && (
                <Message variant="danger" duration={10}>
                  {errorImageUpload}
                </Message>
              )} */}
              {/* {uploading ? (
                <div>Uploading...</div>
              ) : (
                <Form.Group controlId="image">
                  <div
                    style={{
                      margin: "0px 0px 10px 0px",
                    }}
                  >
                    Upload Product Image
                  </div>
                  <Row>
                    <Col md={3}>
                      <input
                        accept="image/*"
                        type="file"
                        id="file"
                        onChange={handleFileUpload}
                      />
                      <div
                        className="profile-page-image"
                        style={{
                          alignSelf: "center",
                        }}
                      >
                        <Image
                          src={image}
                          alt={name}
                          title="Click to input file"
                          style={{
                            width: "100%",
                            border: "1px solid #ced4da",
                            marginBottom: "1em",
                            borderRadius: "0.25rem",
                            marginTop: "10px",
                          }}
                        />
                      </div>
                    </Col>
                  </Row>
                </Form.Group>
              )} */}
              {/* multiple images uploading */}
              {errorImagesUpload && (
                <Message variant="danger" duration={10}>
                  {errorImagesUpload}
                </Message>
              )}
              {imagesUploading ? (
                <div>Uploading...</div>
              ) : (
                <Form.Group controlId="image">
                  <div
                    style={{
                      margin: "0px 0px 10px 0px",
                    }}
                  >
                    Upload Product Images
                  </div>
                  <Row>
                    <Col md={3}>
                      <input
                        accept="image/*"
                        type="file"
                        id="file"
                        onChange={handleFilesUpload}
                        multiple
                      />
                    </Col>
                  </Row>
                  <div
                    style={{
                      alignSelf: "center",
                      margin: "10px 0px 10px 0px",
                    }}
                  >
                    {images.map((image) => (
                      <img
                        src={image}
                        alt={name}
                        title="Click to input file"
                        style={{
                          width: "100px",
                          height: "100px",
                          objectFit: "contain",
                          border: "1px solid #ced4da",
                        }}
                      />
                    ))}
                  </div>
                </Form.Group>
              )}
              <Form.Group controlId="brand">
                <FloatingLabel
                  controlId="brandinput"
                  label="Brand"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter brand"
                    type="text"
                    value={brand}
                    onChange={(e) => setBrand(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="category">
                <FloatingLabel
                  controlId="categoryinput"
                  label={`Selected-Category: ${category}`}
                  className="mb-3"
                >
                  <Select
                    value={category}
                    onChange={(e) => setCategory(e.value)}
                    options={options}
                    defaultValue={category}
                    placeholder={category}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="subCategory">
                <FloatingLabel
                  controlId="subCategoryinput"
                  label={`Selected-SubCategory: ${subCategory}`}
                  className="mb-3"
                >
                  <Select
                    value={subCategory}
                    onChange={(e) => setSubCategory(e.value)}
                    options={subCategoryOptions}
                    defaultValue={subCategory}
                    placeholder={subCategory}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="sizes">
                <FloatingLabel
                  controlId="sizeinput"
                  label="Available Sizes"
                  className="mb-3"
                >
                  <MultiSelect
                    options={displayOptions}
                    selectedValues={sizesAvailable}
                    onSelect={onSelect}
                    onRemove={onRemove}
                    displayValue="label"
                    showCheckbox={true}
                    placeholder="Select Available Sizes"
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="description">
                <FloatingLabel
                  controlId="descinput"
                  label="Description"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter description URL"
                    type="text"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="countInStock">
                <FloatingLabel
                  controlId="countinstockinput"
                  label="CountInStock"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter Count In Stock"
                    type="number"
                    min="0"
                    max="1000"
                    value={countInStock}
                    onChange={(e) => setCountInStock(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="numreviews">
                <FloatingLabel
                  controlId="numreviewsinput"
                  label="NumOfReviews"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter Product Rating"
                    type="number"
                    min="0"
                    max="20"
                    value={numReviews}
                    onChange={(e) => setNumReviews(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <Form.Group controlId="numreviews">
                <FloatingLabel
                  controlId="dealerinput"
                  label="Enter Dealer Name"
                  className="mb-3"
                >
                  <Form.Control
                    placeholder="Enter Dealer Name"
                    type="text"
                    value={dealer}
                    onChange={(e) => setDealer(e.target.value)}
                  />
                </FloatingLabel>
              </Form.Group>
              <div className="d-flex">
                <Button type="submit" className="my-1 ms-auto">
                  Update Product
                </Button>
              </div>
            </Form>
          )}
        </>
      )}
    </FormContainer>
  </>
);
};

export default ProductEditPage;
